import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import {
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
} from "@material-ui/core";

import { KeyboardDatePicker } from "@material-ui/pickers";

export default function FormDialog({ lot, handleSnackbar, setMaj, maj }) {
  const [open, setOpen] = useState(false);
  const [stats, setStats] = useState({
    date: new Date().toISOString().substring(0, 10),
    typeStat: "",
    quantitelb: "",
    commentaire: "",
  });

  const handleConfirm = () => {
    var url = "/api/stats";
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ ...stats, lot: lot }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    setMaj(~maj);
    setOpen(false);
    setStats({
      date: new Date().toISOString().substring(0, 10),
      typeStat: "",
      quantitelb: "",
      commentaire: "",
    });
  };

  return (
    <div>
      <Button
        id="ButtonDialog"
        variant="contained"
        color="secondary"
        onClick={() => setOpen(true)}
      >
        Añadir estadistica
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Añadir estadistica</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Elija la fecha de la estadistica y la cantidad por libras.
          </DialogContentText>
          <Grid container spacing={1} justify="center" alignItems="center">
            <Grid item xs={12}>
              <KeyboardDatePicker
                disableToolbar
                variant="inline"
                format="dd/MM/yyyy"
                margin="normal"
                id="date-picker-inline"
                label="Date"
                value={stats.date}
                onChange={(date) => {
                  setStats({
                    ...stats,
                    date: date.toISOString().substring(0, 10),
                  });
                }}
                KeyboardButtonProps={{
                  "aria-label": "change date",
                }}
              />
            </Grid>
            <Grid item xs={12}>
              <FormControl style={{ minWidth: "200px" }} required>
                <InputLabel id="Type-label">Typo de estadistica</InputLabel>
                <Select
                  labelId="Type-label"
                  id="Type-select"
                  value={stats.typeStat}
                  onChange={(e) =>
                    setStats({ ...stats, typeStat: e.target.value })
                  }
                >
                  <MenuItem value={"S"}>S</MenuItem>
                  <MenuItem value={"E"}>E</MenuItem>
                </Select>
              </FormControl>
            </Grid>
            <Grid item xs={12}>
              <TextField
                required
                label="Cantitad por libras"
                type="number"
                value={stats.quantitelb}
                onChange={(e) =>
                  setStats({ ...stats, quantitelb: e.target.value })
                }
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="Comentario"
                type="text"
                value={stats.commentaire}
                onChange={(e) =>
                  setStats({ ...stats, commentaire: e.target.value })
                }
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
            }}
            color="primary"
          >
            Annuler
          </Button>
          <Button onClick={handleConfirm} color="primary">
            Ajouter
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
