import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import { Grid } from "@material-ui/core";
import { KeyboardDatePicker } from "@material-ui/pickers";

export default function FormDialog({
  espece,
  lot,
  handleSnackbar,
  setMaj,
  maj,
}) {
  const [open, setOpen] = useState(false);
  const [semis, setSemis] = useState({
    date: new Date().toISOString().substring(0, 10),
    poids: "",
    quantitelb: "",
    commentaire: "",
    quantite: "",
  });
  const handleConfirm = () => {
    console.log(lot, semis);
    var url = "/api/semis";
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        ...semis,
        quantite:
          espece.substring(0, 10) === "Postlarves"
            ? semis.quantite
            : semis.quantitelb * semis.poids,
        poids:
          espece.substring(0, 10) === "Postlarves"
            ? semis.quantite / semis.quantitelb
            : semis.poids,
        lot: lot,
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    setMaj(~maj);
    setOpen(false);
    setSemis({
      date: new Date().toISOString().substring(0, 10),
      typePoids: "",
      poids: "",
      quantitelb: "",
      commentaire: "",
    });
  };

  return (
    <div>
      <Button
        id="ButtonDialog"
        variant="contained"
        color="secondary"
        onClick={() => setOpen(true)}
      >
        Añadir siembra
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Añadir una siembra</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Elija la fecha de siembra y la cantitad.
          </DialogContentText>
          <Grid container spacing={1} justify="center" alignItems="center">
            <Grid item xs={12}>
              <KeyboardDatePicker
                disableToolbar
                variant="inline"
                format="dd/MM/yyyy"
                margin="normal"
                id="date-picker-inline"
                label="Fecha"
                value={semis.date}
                onChange={(date) => {
                  console.log(date.toISOString().substring(0, 10));
                  setSemis({
                    ...semis,
                    date: date.toISOString().substring(0, 10),
                  });
                }}
                KeyboardButtonProps={{
                  "aria-label": "change date",
                }}
              />
            </Grid>
            <Grid item xs={12}>
              {espece && espece.substring(0, 10) === "Postlarves" ? (
                <TextField
                  required
                  label="Cantidad total"
                  type="number"
                  value={semis.quantite}
                  onChange={(e) =>
                    setSemis({ ...semis, quantite: parseFloat(e.target.value) })
                  }
                />
              ) : (
                <TextField
                  required
                  label="Peso total"
                  type="number"
                  value={semis.poids}
                  onChange={(e) =>
                    setSemis({ ...semis, poids: parseFloat(e.target.value) })
                  }
                />
              )}
            </Grid>
            <Grid item xs={12}>
              <TextField
                required
                label="Cantitad por libras"
                type="number"
                value={semis.quantitelb}
                onChange={(e) =>
                  setSemis({ ...semis, quantitelb: parseFloat(e.target.value) })
                }
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="Comentario"
                type="text"
                value={semis.commentaire}
                onChange={(e) =>
                  setSemis({ ...semis, commentaire: e.target.value })
                }
              />
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
            }}
            color="primary"
          >
            Annuler
          </Button>
          <Button onClick={handleConfirm} color="primary">
            Ajouter
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
