import React, { useState } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import { authFetch } from "auth";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import {
  FormControl,
  Grid,
  InputLabel,
  MenuItem,
  Select,
} from "@material-ui/core";
import { KeyboardDatePicker } from "@material-ui/pickers";

export default function FormDialog({ lot, handleSnackbar, setMaj, maj }) {
  const [open, setOpen] = useState(false);
  const [peches, setPeches] = useState({
    date: new Date().toISOString().substring(0, 10),
    poids: "",
    quantitelb: "",
    commentaire: "",
    destination: "",
  });

  const handleConfirm = () => {
    console.log(lot, peches);
    var url = "/api/peches";
    const requestOptions = {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        ...peches,
        quantite: peches.quantitelb * peches.poids,
        lot: lot,
      }),
    };
    authFetch(url, requestOptions)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        handleSnackbar("success", response.message);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    setMaj(~maj);
    setOpen(false);
    setPeches({
      date: new Date().toISOString().substring(0, 10),
      poids: "",
      quantitelb: "",
      commentaire: "",
      destination: "",
    });
  };

  return (
    <div>
      <Button
        id="ButtonDialog"
        variant="contained"
        color="secondary"
        onClick={() => setOpen(true)}
      >
        Añadir una pesca
      </Button>
      <Dialog
        open={open}
        onClose={() => {
          setOpen(false);
        }}
        aria-labelledby="form-dialog-title"
      >
        <DialogTitle id="form-dialog-title">Añadir una pesca</DialogTitle>
        <DialogContent>
          <DialogContentText>
            Elija la fecha de siembra, el peso total y la cantitad por libras.
          </DialogContentText>
          <Grid container spacing={1} justify="center" alignItems="center">
            <Grid item xs={12}>
              <KeyboardDatePicker
                disableToolbar
                variant="inline"
                format="dd/MM/yyyy"
                margin="normal"
                id="date-picker-inline"
                label="Date"
                value={peches.date}
                onChange={(date) => {
                  setPeches({
                    ...peches,
                    date: date.toISOString().substring(0, 10),
                  });
                }}
                KeyboardButtonProps={{
                  "aria-label": "change date",
                }}
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                required
                label="Peso total"
                type="number"
                value={peches.poids}
                onChange={(e) =>
                  setPeches({ ...peches, poids: e.target.value })
                }
              />
            </Grid>
            {/* <Grid item xs={12}>
              <FormControl style={{ minWidth: "200px" }} required>
                <InputLabel id="Type-label">Type de poids</InputLabel>
                <Select
                  labelId="Type-label"
                  id="Type-select"
                  value={peches.typePoids}
                  onChange={(e) =>
                    setPeches({ ...peches, typePoids: e.target.value })
                  }
                >
                  <MenuItem value={"quantitelb"}>Quantite par livre</MenuItem>
                  <MenuItem value={"grpoisson"}>Gramme par poisson</MenuItem>
                  <MenuItem value={"poids"}>Poids total</MenuItem>
                </Select>
              </FormControl>
            </Grid> */}
            <Grid item xs={12}>
              <TextField
                required
                label={"Cantitad por libras"}
                type="number"
                value={peches.quantitelb}
                onChange={(e) =>
                  setPeches({ ...peches, quantitelb: e.target.value })
                }
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                label="Comentario"
                type="text"
                value={peches.commentaire}
                onChange={(e) =>
                  setPeches({ ...peches, commentaire: e.target.value })
                }
              />
            </Grid>
            <Grid item xs={12}>
              <FormControl style={{ minWidth: "200px" }} required>
                <InputLabel id="Destination-label">Destino</InputLabel>
                <Select
                  labelId="Destination-label"
                  id="Destination-select"
                  value={peches.destination}
                  onChange={(e) =>
                    setPeches({ ...peches, destination: e.target.value })
                  }
                >
                  <MenuItem value={"V"}>Venta</MenuItem>
                  <MenuItem value={"S"}>Siembra</MenuItem>
                  <MenuItem value={"A"}>Otro</MenuItem>
                </Select>
              </FormControl>
            </Grid>
          </Grid>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => {
              setOpen(false);
            }}
            color="primary"
          >
            Annuler
          </Button>
          <Button onClick={handleConfirm} color="primary">
            Ajouter
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
