import React, { useState, useEffect } from "react";
import { authFetch } from "auth";
import Button from "@material-ui/core/Button";
import {
  Grid,
  FormControl,
  Select,
  InputLabel,
  MenuItem,
  FormHelperText,
  Card,
  CardHeader,
  Typography,
} from "@material-ui/core";

import Snackbar from "components/Snackbar/Snackbar.js";
import CustomTableDelete from "./TableDelete";
import CardBody from "./Card/CardBody";
import AddSemisDialog from "./Dialog/AddSemisDialog";
import AddPechesDialog from "./Dialog/AddPechesDialog";
import AddStatsDialog from "./Dialog/AddStatsDialog";
import AddLotDialog from "./Dialog/AddLotDialog";

export default function Lots({ cycle_, resetCycle }) {
  const [cycle, setCycle] = useState(cycle_.toString());
  const [lot, setLot] = useState();
  const [comm, setComm] = useState("");
  const [lotsList, setLotsList] = useState([]);
  const [lotsMenu, setLotsMenu] = useState([]);

  const [tc, setTC] = useState(false);
  const [message, setMessage] = useState("");
  const [color, setColor] = useState("primary");
  const [data, setData] = useState({ peches: [], semis: [], statistiques: [] });
  const [espece, setEspece] = useState("Tilapia");
  const [maj, setMaj] = useState(true);
  const [majLots, setMajLots] = useState(true);
  const [total, setTotal] = useState(undefined);

  useEffect(() => {
    var url = "/api/lots";
    authFetch(url)
      .then((response) => {
        if (response.ok) {
          return response.json();
        } else {
          throw new Error(response.statusText);
        }
      })
      .then((response) => {
        setLotsList(response.lotsList);
        setLotsMenu([]);
      })
      .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
  }, [cycle, majLots]);

  useEffect(() => {
    let temp = [];
    for (const lots of lotsList) {
      if (lots[1].toString() === cycle.toString()) {
        setLot(lots[2]);
        setComm(lots[3]);
        temp = [lots[2], ...temp];
      }
    }
    setLotsMenu(temp);
  }, [lotsList, cycle]);

  const handleSnackbar = (cl, ms) => {
    setColor(cl);
    setMessage(ms);
    setTC(true);
    setTimeout(function () {
      setTC(false);
      setColor("");
      setMessage("");
    }, 4000);
  };

  useEffect(() => {
    if (lot !== undefined) {
      var url = "/api/lotData/" + lot;
      authFetch(url)
        .then((response) => {
          if (response.ok) {
            return response.json();
          } else {
            throw new Error(response.statusText);
          }
        })
        .then((response) => {
          setEspece(response.espece ? response.espece[0] : "");
          setData({
            peches: response.peches,
            semis: response.semis,
            statistiques: response.statistiques,
          });
          setTotal(response.total);
        })
        .catch((e) => handleSnackbar("danger", e.name + ": " + e.message));
    }
  }, [lot, maj]);

  return (
    <div id="lot">
      <Snackbar
        place="tc"
        color={color}
        message={message}
        open={tc}
        closeNotification={() => setTC(false)}
        close
      />
      <Grid container spacing={1} justify="center" alignItems="center">
        <Grid item xs={2} align="center">
          <Typography variant="h4">{espece}</Typography>
        </Grid>
        <Grid item xs={2} align="center">
          <Typography variant="p">{comm}</Typography>
        </Grid>
        <Grid item xs={2} align="center">
          <FormControl>
            <InputLabel id="cycle-label">Ciclo</InputLabel>
            <Select
              labelId="cycle-label"
              id="cycle-select"
              value={cycle}
              onChange={(e) => setCycle(e.target.value)}
            >
              {lotsList.map((Cycle, key) => (
                <MenuItem key={key} value={Cycle[1]}>
                  {Cycle[0] + "#" + Cycle[1]}
                </MenuItem>
              ))}
            </Select>
            <FormHelperText>Seleccione un ciclo</FormHelperText>
          </FormControl>
        </Grid>
        <Grid container spacing={1} item xs={4} align="center" justify="center">
          {lotsMenu.map((Lot, key) => (
            <Grid key={key} item>
              <Button
                key={key + 1000}
                variant="contained"
                color="secondary"
                onClick={() => setLot(Lot)}
              >
                Lote #{Lot}
              </Button>
            </Grid>
          ))}
          <Grid item>
            <AddLotDialog
              cycle={cycle}
              handleSnackbar={handleSnackbar}
              setMaj={setMajLots}
              maj={majLots}
            />
          </Grid>
          <Grid item>
            <Button variant="contained" color="primary" onClick={resetCycle}>
              Back
            </Button>
          </Grid>
        </Grid>
        <Grid item xs={6} container align="center">
          {/* Semis */}
          <Card>
            <CardHeader
              style={{ padding: "8px", paddingRight: "16px" }}
              id="Header"
              color="secondary"
              title="Siembra"
              action={
                <AddSemisDialog
                  lot={lot}
                  handleSnackbar={handleSnackbar}
                  setMaj={setMaj}
                  maj={maj}
                  espece={espece}
                />
              }
            />
            <CardBody style={{ padding: 0 }}>
              <CustomTableDelete
                tableHead={[
                  "date",
                  "quantite",
                  "poids",
                  "quantitelb",
                  "grpoisson",
                  "commentaire",
                ]}
                tableHeaderColor="primary"
                tableData={data.semis}
                postUrl={"/api/lotData/Semis"}
                newLine={true}
                info_for_post={lot}
              />
            </CardBody>
          </Card>
        </Grid>
        <Grid item xs={6} container align="center">
          {/* Statistiques */}
          <Card>
            <CardHeader
              style={{ padding: "8px", paddingRight: "16px" }}
              id="Header"
              color="info"
              title="Estadísticas"
              action={
                <AddStatsDialog
                  lot={lot}
                  handleSnackbar={handleSnackbar}
                  setMaj={setMaj}
                  maj={maj}
                />
              }
            />
            <CardBody style={{ padding: 0 }}>
              <CustomTableDelete
                tableHead={[
                  "date",
                  "typestat",
                  "quantitelb",
                  "grpoisson",
                  "commentaire",
                ]}
                tableHeaderColor="primary"
                tableData={data.statistiques}
                postUrl={"/api/lotData/Statistiques"}
                newLine={true}
                info_for_post={lot}
              />
            </CardBody>
          </Card>
        </Grid>
        <Grid item xs={6} container align="center">
          {/* Peches */}
          <Card>
            <CardHeader
              style={{ padding: "8px", paddingRight: "16px" }}
              id="Header"
              color="primary"
              title="Pescas"
              action={
                <AddPechesDialog
                  lot={lot}
                  handleSnackbar={handleSnackbar}
                  setMaj={setMaj}
                  maj={maj}
                />
              }
            />
            <CardBody style={{ padding: 0 }}>
              <CustomTableDelete
                tableHead={[
                  "date",
                  "quantite",
                  "poids",
                  "quantitelb",
                  "grpoisson",
                  "commentaire",
                  "destination",
                ]}
                tableHeaderColor="primary"
                tableData={data.peches}
                postUrl={"/api/lotData/Peches"}
                newLine={true}
                info_for_post={lot}
              />
            </CardBody>
          </Card>
        </Grid>
        <Grid item xs={6} container align="center">
          {/* Peches */}
          <Card>
            <CardHeader
              style={{ padding: "8px", paddingRight: "16px" }}
              id="Header"
              color="primary"
              title="Total"
            />
            <CardBody style={{ padding: 0 }}>
              {total ? (
                <div>
                  <h4>
                    Total semis: {total.totalSemis[1]} animaux pour un poids de{" "}
                    {total.totalSemis[0]} livres
                  </h4>
                  <h4>Total peches: {total.totalPeches} livres</h4>

                  <h4>
                    Total actuel: Apres {total.totalActuel[2] / 4} mois il y a{" "}
                    {total.totalActuel[0]} animaux pour un poids de{" "}
                    {total.totalActuel[1]} livres. La mortalité est de{" "}
                    {total.totalActuel[3] * 100} %
                  </h4>
                </div>
              ) : null}
            </CardBody>
          </Card>
        </Grid>
      </Grid>
    </div>
  );
}
